<template>
    <div class="search" @keyup.enter="search">
        <form-create v-model="formObj" :rule="rule" :option="options" @btn-search="search" @btn-reset="reset"
            @kp-code-select="handleSelectKp">
        </form-create>
        <div class="li">
            <el-button type="primary" @click="down"> </el-button>
        </div>
    </div>
</template>
<script>
    //
    import {
        getGlList,
        getKpList,
        getKcList,
        getPointsList
    } from "@/api/archives.js";
    import {
        parseTime,
        getPrevMonth
    } from "@/utils/index";
    import form from "../billCreate/SearchInputsCreate.vue";
    export default {
        extends: form,
        data() {
            return {
                //
                rule: [{
                    type: 'select',
                    field: "kcCode",
                    title: '',
                    value: '',
                    options: [],
                    props: {
                        filterable: true,
                    },
                    on: {
                        change: (item) => {
                            //  
                            const formData = this.formObj.bind();
                            formData.glCode = '';
                            if (this._findRule('kpCode')) this.formObj.exec('kpCode', 'clear')
                            if (this._findRule('jpCode')) this.formObj.exec('jpCode', 'clear')
                        }
                    },
                    col: {
                        span: 6
                    }
                }, {
                    type: 'AutoCompleteQuery',
                    field: 'kpCode',
                    title: '',
                    props: {
                        placeholder: '',
                        fieldValue: 'code',
                        fieldLabel: 'name',
                        value: '',
                        queryFunc: (keyWord) => {
                            const opt = {
                                pageNum: 1,
                                pageSize: 20,
                                kcCode: this.formObj.getValue('kcCode'),
                                _loading: false, //
                            }
                            if (keyWord !== '') {
                                opt.keyWord = keyWord;
                            }
                            return getKpList(opt).then(res => {
                                if (res.data) {
                                    const list = res.data.map(item => ({
                                        ...item,
                                        value: item.name,
                                    }))
                                    return list
                                } else {
                                    return []
                                }
                            })
                        }
                    },
                    emit: ['select'],
                    // emitPrefix: 'kp',
                    col: {
                        span: 6
                    }
                }, {
                    type: 'select',
                    field: "glCode",
                    title: '',
                    value: '',
                    options: [],
                    on: {
                        change: (item) => {
                            //  
                            const formData = this.formObj.bind();
                            formData.kcCode = '';
                            if (this._findRule('kpCode')) this.formObj.exec('kpCode', 'clear')
                            if (this._findRule('jpCode')) this.formObj.exec('jpCode', 'clear')
                        }
                    },
                    col: {
                        span: 6
                    }
                }, {
                    type: 'SubmitBtns',
                    field: 'btn',
                    emit: ['search', 'reset'],
                    col: {
                        span: 6
                    }
                }, {
                    type: 'select',
                    field: "billState",
                    title: '',
                    value: '7',
                    options: [{
                        value: '7',
                        label: '',
                    }, {
                        value: '3',
                        label: '',
                    }, {
                        value: '8',
                        label: '',
                    }],
                    col: {
                        span: 6
                    }
                }, {
                    type: "DatePicker",
                    field: "month",
                    title: "",
                    value: getPrevMonth(), //,
                    props: {
                        type: "month",
                        format: "yyyyM",
                        placeholder: "",
                        pickerOptions: {
                            disabledDate(time) {
                                return new Date(time) > new Date().getTime()
                            }
                        },
                    },
                    col: {
                        span: 6
                    }
                }, {
                    type: 'select',
                    field: "type",
                    title: '',
                    value: '',
                    options: [{
                        value: '',
                        label: '',
                    }, {
                        value: '1',
                        label: '',
                    }, {
                        value: '2',
                        label: '',
                    }, {
                        value: '3',
                        label: '',
                    }, {
                        value: '4',
                        label: '',
                    }],
                    col: {
                        span: 6
                    }
                }, {
                    type: 'ElCol',
                    props: {
                        span: 6,
                    },
                }, {
                    type: 'input',
                    field: "gcAccount",
                    title: '',
                    value: '',
                    props: {
                        placeholder: '',
                    },
                    col: {
                        span: 6
                    }
                }]
            }
        },
        mounted() {
            this.search();
        },
        methods: {
            search() {
                const formData = this.formObj.formData();
                const opt = {
                    glCode: formData.glCode, // 
                    kcCode: formData.kcCode, //
                    kpCode: formData.kpCode, //
                    type: formData.type,
                    billState: formData.billState,
                    month: parseTime(formData.month, '{y}-{m}'),
                }
                for (let key in opt) {
                    if (opt[key] == '' || opt[key] == undefined) delete opt[key];
                }
                this.$emit('searchHand', opt)
            }
        }
    }
</script>