<!---->
<template>
    <div>
        <SearchInputs @downHand="downHand" @searchHand="searchHand"></SearchInputs>
        <CusTable :tableData="tableData" @btnClick="btnClick" @handleSizeChange="handleSizeChange"
            @handleCurrentChange="handleCurrentChange" :pageNum="page.pageNum" :pageSize="page.pageSize" :total="total">
        </CusTable>

        <!---->
        <ExamModel :show="examModelVisible" :detailData="detailData" :loading="loading" @examHand="examHand">
        </ExamModel>

        <!---->
        <ErrorDlModel :show="errorModelVisible" :detailData="detailData" :loading="loading" @errorHand="errorHand"
            :type="1">
        </ErrorDlModel>

        <!---->
        <SubTableDataInput :show="dataInputModelVisible" :detailData="detailData" :loading="loading"
            @dataInputHand="dataInputHand">
        </SubTableDataInput>

        <!---->
        <BaseEditModel :show="editModelVisible" :type="editModelType" :detailData="detailData" :loading="loading"
            @editHand="editHand">
        </BaseEditModel>

        <!--  -->
        <el-dialog :title="tableDialogType==2?'':''" custom-class="cusDialog"
            :visible.sync="showTableDialog" width="10rem" :before-close="handleClose" @closed="closed">
            <div class="print-body">
                <TempalteBill v-if="showPrintWd&&tableDialogType==1" :type="4" :tableData="detailData"></TempalteBill>
                <TempalteBill2 v-if="showPrintWd&&tableDialogType==2" :type="1" :tableData="detailData"></TempalteBill2>
            </div>
            <span slot="footer" class="dialog-footer">
                <el-button type="primary" @click="html2Excel"> </el-button>
                <el-button type="primary" v-print="printObj"> </el-button>
                <el-button @click="handleClose"> </el-button>
            </span>
        </el-dialog>
    </div>
</template>
<script>
    //
    import TableCommon from "@/components/TableCommon.vue";
    import CusTable from "./components/billCreate/BillCreateTable.vue";
    import SearchInputs from "./components/billCreate/SearchInputsCreate.vue";
    import ExamModel from "./components/billCreate/ExamModel.vue"; //
    import ErrorDlModel from "./components/common/ErrorDlModel.vue"; //
    import SubTableDataInput from "./components/billCreate/SubTableDataInput.vue"; //
    import BaseEditModel from "./components/billCreate/BaseEditModel.vue"; //
    import TempalteBill from "@/components/tempalteBill.vue"; //
    import {
        parseTime
    } from "@/utils/index";
    //
    import {
        getComfirmUnApplyList, //
        postComfirmApply, //
        postBillException, //
        postBillUpdate, //
        exportBill, //
        getEditWell,
        postStateGridFile, //

    } from "@/api/archives.js";
    //
    import tool from "@/utils/tool";
    import {
        mapState
    } from 'vuex';
    import Table2Excel from 'table2excel.js'
    export default {
        extends: TableCommon,
        name: 'BillCreate',
        components: {
            CusTable,
            SearchInputs,
            ExamModel, //
            ErrorDlModel, //
            SubTableDataInput, //
            TempalteBill, //
            BaseEditModel, //
            TempalteBill2: TempalteBill, //
        },
        data() {
            return {
                //
                examModelVisible: false, //
                errorModelVisible: false, //
                dataInputModelVisible: false, //  
                editModelType: 1, // 1 2
                editModelVisible: false, //
                showTableDialog: false, // 
                tableDialogType: 1, //

                detailData: {}, //
                showPrintWd: false, //
                printObj: { //
                    id: "printArea",
                    popTitle: '',
                }
            }
        },
        created() {

        },
        computed: {
            ...mapState({
                userId: state => state.user.userId, //user
            })
        },
        mounted() {
            //
            //this._getData();
        },
        methods: {
            parseTime,
            isYes: tool.isYes,
            _getData(params = {}) { //    
                params = {
                    ...this.search,
                    ...this.page,
                    billState: '',
                }
                getComfirmUnApplyList(params).then(res => {
                    const data = res.rows ? res.rows : [];
                    //
                    data.forEach((item, index) => {
                        item.index = (this.page.pageNum - 1) * this.page.pageSize + index + 1;
                        if (item.type == 4) { // 
                            item.index += ' ';
                        }
                        if (item.subBills && item.subBills.length) {
                            item.subBills.forEach((p, k) => {
                                p.index = item.index + '.' + (k + 1);
                                p.isChild = true;
                            })
                        }
                    });
                    this.tableData = data;
                    this.total = res.total;
                })
            },
            //
            downHand() {
                exportBill({
                    ...this.search,
                    billState: '1',
                }).then(tool.down)
            },
            btnClick(name, row) {
                this.detailData = {
                    ...row
                };
                if (name == "") {
                    this.examModelVisible = true;
                } else if (name == "") {
                    // this.$message.warning('')
                    // return;
                    this.errorModelVisible = true;
                } else if (name == "") { // 
                    if (this.isYes(row.stateGirdPaperImp)) { //  2
                        this.editModelType = 2;
                        this.editModelVisible = true;
                    } else if (this.isYes(row.dayPaperImp)) { //
                        this.dataInputModelVisible = true;
                    }
                    //
                } else if (name == '') {
                    this.showPrintWd = true;
                    this.tableDialogType = 1; //
                    this.showTableDialog = true;

                } else if (name == '') {
                    this.showPrintWd = true;
                    this.tableDialogType = 2; //
                    this.showTableDialog = true;

                } else if (name == '') { //  1 2
                    this.editModelType = 1;
                    this.editModelVisible = true;
                }
            },
            handleClose() {
                this.showTableDialog = false;
            },
            //
            errorHand(row, data) {
                if (!row) {
                    this.errorModelVisible = false;
                    return;
                }
                this.loading = true;
                postBillException({
                    billId: row.id,
                    userId: this.userId,
                    solveType: data.solveType,
                    reason: data.reason,
                    date: this.parseTime(new Date()),
                }).then(res => {
                    this.loading = false; //
                    this.errorModelVisible = false; //
                    this.resPublick(res);
                })
            },
            editHand(data, type) {
                if (!data) {
                    this.editModelVisible = false;
                    return;
                }
                this.loading = true;
                if (type == 1) {
                    getEditWell({
                        id: this.detailData.id,
                        wellName: data.kwell,
                    }).then(res => {
                        this.loading = false; //
                        this.editModelVisible = false;
                        this.resPublick(res);
                    })
                } else {
                    postStateGridFile(data).then(res => {
                        this.loading = false; //
                        this.editModelVisible = false;
                        this.resPublick(res);
                    })
                }
            },
            //
            examHand(row) {
                if (!row) {
                    this.examModelVisible = false;
                    return;
                }
                //
                if (row.subBills && row.subBills.length) {
                    const all = row.subBills.length == 1 ? row.subBills[0].allEq : row.subBills.reduce((a, b) => {
                        return a + b.allEq
                    }, 0)
                    if (all !== row.allEq) {
                        this.$message({
                            type: 'warning',
                            message: ''
                        })
                        return;
                    }
                }
                this.loading = true;
                postComfirmApply({
                    userId: this.userId,
                    id: row.id,
                }).then(res => {
                    this.loading = false; //  
                    this.examModelVisible = false; //
                    this.resPublick(res);
                }).catch(err => {
                    setTimeout(() => {
                        this.loading = false; //
                    }, 1000);
                })
            },
            //
            dataInputHand(data) {
                if (!data) {
                    this.dataInputModelVisible = false;
                    return;
                }
                if (this.detailData.parentId) { //
                    //
                    const parentData = this.tableData.find(item => item.id == this.detailData.parentId)
                    if (data.allEq > parentData.allEq) {
                        this.$message.warning('')
                        return;
                    }
                }
                this.loading = true;
                //
                if (this.detailData.type == 4) {
                    postStateGridFile(data).then(res => {
                        this.loading = false; //
                        this.dataInputModelVisible = false;
                        this.resPublick(res);
                    }).catch(err => {
                        this.loading = false; //
                    })
                } else { //
                    postBillUpdate(data).then(res => {
                        this.loading = false; //
                        this.dataInputModelVisible = false; //
                        this.resPublick(res);
                    }).catch(err => {
                        this.loading = false; //
                    })
                }
            },
            //excel
            html2Excel() {
                const name = this.detailData.kwell ? this.detailData.kwell : this.detailData.kpName;
                new Table2Excel('#printArea table').export(
                    `${name}-${this.detailData.month}-${this.tableDialogType==1?'':''}`);
            },
            //
            closed() {
                this.showPrintWd = false;
            },
            //
            resPublick(res) {
                this.loading = false; //
                this.$message({
                    type: 'success',
                    message: ''
                })
                this.reloadTable();
            },
        }
    }
</script>