<template>
    <!---->
    <el-dialog :title="title" custom-class="cusDialog" :visible.sync="show" width="5rem" :before-close="handleClose">
        <el-form class="form" :model="selectedObj" :rules="rules" ref="form">
            <div class="li">
                <div class="item">
                    <el-form-item>
                        <div class="label"></div>
                        <div class='rt'>
                            <div class="box">{{selectedObj.kpName}}</div>
                        </div>
                    </el-form-item>
                </div>
            </div>
            <div class="li">
                <div class="item">
                    <el-form-item>
                        <div class="label"></div>
                        <div class='rt'>
                            <div class="box">{{selectedObj.sdName}}</div>
                        </div>
                    </el-form-item>
                </div>
            </div>
            <div class="li">
                <div class="item">
                    <div class="label"></div>
                    <div class='rt'>
                        <div class="box">{{ changeMonth(selectedObj.month)}}</div>
                    </div>
                </div>
            </div>
            <div class="li">
                <div class="item">
                    <div class="label"></div>
                    <div class='rt'>
                        <div class="box">
                            {{ parseTime(selectedObj.arSt,'{y}/{m}/{d} {h}:{i}') + "-"+ parseTime(selectedObj.arEt,'{y}/{m}/{d} {h}:{i}')}}
                        </div>
                    </div>
                </div>
            </div>
            <!---->
            <div class="li" v-if="type==1||type==4">
                <div class="item">
                    <el-form-item prop="arSnum">
                        <div class="label"><span class="important">*</span></div>
                        <el-input type="number" :min="0" :max="selectedObj.arEnum" v-model="selectedObj.arSnum"
                            placeholder=""></el-input>
                    </el-form-item>
                </div>
            </div>

            <div class="li" v-if="type==1||type==4">
                <div class="item">
                    <el-form-item prop="arEnum">
                        <div class="label"><span class="important">*</span></div>
                        <el-input type="number" @blur="inputE" :min="selectedObj.arSnum" v-model="selectedObj.arEnum"
                            placeholder=""></el-input>
                    </el-form-item>
                </div>
            </div>
            <div class="li" v-if="type==1||type==4">
                <div class="item">
                    <div class="label"></div>
                    <div class='rt'>
                        <div class="box">{{selectedObj.blRate}}</div>
                    </div>
                </div>
            </div>

            <!---->
            <div class="li" v-if="type==2">
                <div class="item">
                    <el-form-item prop="arSnum">
                        <div class="label"><span class="important">*</span></div>
                        <el-upload class="upload-demo" :action="fileUploadUrl" :headers="headers"
                            :on-success="handleChange" :before-upload="beforeUpload" :on-error="error" :data="params"
                            :show-file-list="false">
                            <a :loading="upLoading" class="fileName"
                                :title="excelName">{{fileList.length?excelName:''}}
                            </a>
                        </el-upload>
                    </el-form-item>
                </div>
            </div>
            <div class="li" v-if="type==2">
                <div class="item">
                    <el-form-item>
                        <div class="label"></div>
                        <span v-if="fileList.length" class="unit">{{selectedObj.drEq}} kWh <small
                                class="notice">:{{calc}}</small></span>
                        <span v-else class="unit red"></span>
                    </el-form-item>
                </div>
            </div>
            <div class="li" v-if="type==2">
                <div class="item">
                    <el-form-item prop="arSnum">
                        <div class="label"><span class="important">*</span></div>
                        <el-input type="number" :min="0" @input="inputS" v-model="selectedObj.arSnum"
                            placeholder="">
                        </el-input>
                    </el-form-item>
                </div>
            </div>
            <div class="li" v-if="type==2">
                <div class="item">
                    <el-form-item prop="arEnum">
                        <div class="label"><span class="important">*</span></div>
                        <el-input type="number" @blur="inputE" :min="selectedObj.arSnum" v-model="selectedObj.arEnum"
                            placeholder=""></el-input>
                    </el-form-item>
                </div>
            </div>
            <div class="li" v-if="type==2">
                <div class="item">
                    <div class="label"></div>
                    <div class='rt'>
                        <div class="box">{{selectedObj.blRate}}</div>
                    </div>
                </div>
            </div>


            <div class="li" v-if="type==3||type==4">
                <div class="item">
                    <div class="label"><span class="important">*</span></div>
                    <div class='rt'>
                        <el-upload :class="{hide:filesFP.length,'FPfile':true}" :action="fileUploadUrlFP"
                            :headers="headers" :on-success="handleChangeFP" :before-upload="beforeUploadFP"
                            list-type="picture-card" :limit="2" :file-list="filesFP">
                            <i slot="default" class="el-icon-plus"></i>
                            <div slot="file" slot-scope="{file}">
                                <img v-if="isImg(file)" class="el-upload-list__item-thumbnail" :src="file.url" alt="">
                                <div v-else class="box">
                                    <svg-icon :icon-class="getPreType(file)" class-name="logo-img">
                                    </svg-icon>
                                </div>
                                <span class="el-upload-list__item-actions">
                                    <span v-if="isImg(file)" class="el-upload-list__item-preview"
                                        @click="handlePictureCardPreview(file)">
                                        <i class="el-icon-zoom-in"></i>
                                    </span>
                                    <span class="el-upload-list__item-delete" @click="handleRemove(filesFP,file)">
                                        <i class="el-icon-delete"></i>
                                    </span>
                                </span>
                            </div>
                        </el-upload>
                        <UploadProgress ref="uploadPreogress" />
                    </div>
                </div>
            </div>

            <!---->
            <div class="li" v-if="type!=6&&type!=7">
                <div class="item">
                    <el-form-item>
                        <div class="label"></div>
                        <el-input v-model="selectedObj.loseEq" type="number" min="0" placeholder="">
                            <template slot="append">kWh</template>
                        </el-input>
                    </el-form-item>
                </div>
            </div>

            <div class="li" v-if="type==1||type==2||type==4||type==6||type==7">
                <div class="item">
                    <div class="label"></div>
                    <div class='rt'>
                        <div class="box">{{calcAllEq}}</div>
                    </div>
                </div>
            </div>
            <!---->
            <div class="li" v-if="type==3">
                <div class="item">
                    <el-form-item prop="eqValue">
                        <div class="label"><span class="important">*</span></div>
                        <el-input v-model="selectedObj.eqValue" type="number" min="0" placeholder="">
                            <template slot="append">kWh</template>
                        </el-input>
                    </el-form-item>
                </div>
            </div>
            <div class="li" v-if="type==3">
                <div class="item">
                    <div class="label"></div>
                    <div class='rt'>
                        <div class="box">{{allEqFP}}</div>
                    </div>
                </div>
            </div>
            <!---->

            <div class="li" v-if="type==6|| type==7">
                <div class="item">
                    <div class="label"></div>
                    <div class='rt'>
                        <div class="box contract">
                            <template v-if="contract.name">
                                <div class="li">
                                    <div class="lt" style="width:100%">
                                        <span>{{contract.name}}</span>
                                    </div>
                                </div>
                                <div class="li">
                                    <div class="lt">
                                        <label></label> <span>{{contract.price}} /</span>
                                    </div>
                                    <div class="rt">
                                        <label></label> <span>{{contract.wkTy}}</span>
                                    </div>
                                </div>
                                <div class="li">
                                    <div class="lt">
                                        <label></label> <span>{{contract.devType}}</span>
                                    </div>
                                    <div class="rt">
                                        <label></label> <span>{{contract.taxRate*100}}%</span>
                                    </div>
                                </div>
                            </template>
                            <div class="li" v-else>
                                <div class="lt" style="width:100%">
                                    <span></span>
                                </div>
                            </div>
                        </div>
                    </div>
                </div>
            </div>
            <div class="li" v-if="(type==6 || type==7) && contract.name">
                <div class="item">
                    <el-form-item prop="tempPrice">
                        <div class="label"></div>
                        <el-input v-model="selectedObj.tempPrice" type="number" min="0" placeholder="">
                            <template slot="append">/</template>
                        </el-input>
                    </el-form-item>
                </div>
            </div>
            <div class="li" v-if="type==6|| type==7">
                <div class="item">
                    <div class="label"></div>
                    <div class='rt'>
                        <div class="box">{{totalPrice}}
                            <span class="unit"></span>
                        </div>
                    </div>
                </div>
            </div>
        </el-form>
        <span slot="footer" class="dialog-footer">
            <el-button type="primary" :disabled="canSubmit" :loading="loading" @click="submit"> 
            </el-button>
            <el-button @click="handleClose"> </el-button>
        </span>
    </el-dialog>
</template>
<script>
    import {
        Message
    } from 'element-ui';
    import {
        parseTime,
        formatNum
    } from "@/utils/index";
    import tool from '@/utils/tool'
    import {
        fileUploadUrl,
        resourceUrl,
        billExport,
        getExcepContractQuery
    } from '@/api/archives';
    import {
        baseProfileUrl
    } from '@/api/baseUrl.js';
    import {
        getToken
    } from '@/utils/auth'
    import UploadProgress from "@/components/UploadProgress.vue"; //
    let initObj = {
        id: "", //id
        kpName: '', //
        sdName: '', //
        month: '', //
        arSnum: 0, //
        arEnum: 0, //
        blRate: 0, //
        allEq: 0, //
        loseEq: 0, // 
        drEq: 0, //
        eqValue: 0, //
        tempPrice: '', //
        estimatePrice: '', //
    };
    export default {
        components: {
            UploadProgress,
        },
        props: {
            show: { //
                type: Boolean,
                default: false,
            },
            type: { //
                type: String,
                default: 1,
            },
            detailData: { //
                type: Object,
                default: () => {},
            },
            loading: Boolean
        },
        data() {
            return {

                fileList: [], //
                filesFP: [], //
                upLoading: false, //
                excelName: '', //excel
                headers: {
                    uapAccessToken: getToken('accessToken'),
                    uapRefreshToken: getToken('refreshToken'),
                },
                contract: {}, //
                selectedObj: {
                    ...initObj,
                },
                rules: {
                    blRate: [{
                        validator: (rule, value, callback) => {
                            if (value === '') {
                                callback(new Error(''));
                            } else if (value <= 0) {
                                callback(new Error('0'));
                            } else {
                                callback()
                            }
                        },
                    }],
                    arSnum: [{ //
                        validator: (rule, value, callback) => {
                            if (value === '' || isNaN(value)) {
                                callback(new Error(''));
                            } else if (value < 0) {
                                callback(new Error('0'));
                            } else if (Number(value) > Number(this.selectedObj.arEnum)) {
                                callback(new Error(''))
                            } else {
                                // this.$refs['form'].validateField('arEnum');
                                callback()
                            }
                        },
                        trigger: 'change'
                    }],
                    arEnum: [{ //
                        validator: (rule, value, callback) => {
                            if (value === '' || isNaN(value)) {
                                callback(new Error(''));
                            } else if (value < 0) {
                                callback(new Error('0'));
                            } else if (Number(value) < Number(this.selectedObj.arSnum)) {
                                callback(new Error(''))
                            } else {
                                this.$refs['form'].validateField('arSnum');
                                callback()
                            }
                        },
                        trigger: 'change'
                    }],
                    tempPrice: [{ //
                        validator: (rule, value, callback) => {
                            if (value == '' || value == undefined) return callback();
                            if (isNaN(value) || value != Number(value)) {
                                callback(new Error(''));
                            } else if (value <= 0) {
                                callback(new Error('0'));
                            } else {
                                callback()
                            }
                        },
                        trigger: 'change'
                    }],
                    eqValue: [{ //
                        validator: (rule, value, callback) => {
                            if (value == '') {
                                callback(new Error(''));
                            } else if (value < 0) {
                                callback(new Error('0'));
                            } else {
                                callback()
                            }
                        },
                        trigger: 'change'
                    }],
                },
                textArr: ['', '', '', '', '', '', '', ''],
            }
        },
        mounted() {
            if (!this.detailData) return;
            this.initPage(this.detailData);
        },
        computed: {
            //
            canSubmit() {
                if (this.type <= 4) return false;
                return !(this.contract.name && this.selectedObj.tempPrice)
            },
            fileUploadUrl() {
                return billExport(this.selectedObj.id);
            },
            fileUploadUrlFP() {
                return fileUploadUrl;
            },
            title() {
                return '-' + this.textArr[this.type];
            },
            //- 
            useEq() {
                if (this.selectedObj.arEnum < this.selectedObj.arSnum) {
                    return '-';
                }
                const sum = ((this.selectedObj.arEnum - this.selectedObj.arSnum).toFixed(6) * this.selectedObj.blRate)
                    .toFixed(2);
                return formatNum(sum)
            },
            //=-* + 
            calcAllEq() {
                const sum = ((this.selectedObj.arEnum - this.selectedObj.arSnum).toFixed(6) * this.selectedObj.blRate)
                    .toFixed(2);
                let calc = formatNum(sum) < 0 ? 0 : formatNum(sum);
                return formatNum(calc + Number(this.selectedObj.loseEq))
            },

            allEqFP() { //
                return formatNum(Number(this.selectedObj.eqValue) + Number(this.selectedObj.loseEq));
            },
            calc() { // 
                return Number((this.selectedObj.drEq / this.selectedObj.blRate).toFixed(5));
            },
            totalPrice() {
                let price = (this.selectedObj.tempPrice && this.selectedObj.tempPrice != 0) ? this.selectedObj
                    .tempPrice : this.contract.price
                return price ? formatNum(price * this.calcAllEq) : '-';
            },
            params() { //
                return {
                    id: this.selectedObj.id,
                }
            },
        },
        methods: {
            parseTime,
            initPage(val) { //
                this.selectedObj = {
                    ...initObj
                };
                for (let key in val) {
                    this.selectedObj[key] = val[key];
                }
                if (val.stateGridPaper) {
                    this.filesFP = [{
                        url: this.getShowUrl(val.stateGridPaper),
                        name: this._getFileNameByUrl(val.stateGridPaper),
                    }];
                }
                //
                if (val.dayPaperFile) {
                    this.fileList = [{
                        url: this.getShowUrl(val.dayPaperFile),
                        name: this._getFileNameByUrl(val.dayPaperFile),
                    }];
                    this.excelName = this._getFileNameByUrl(val.dayPaperFile); //excel
                    this.selectedObj.drEq = this.selectedObj.allEq - this.selectedObj.loseEq;
                } else {
                    this.fileList = [];
                    this.excelName = '';
                    this.selectedObj.drEq = 0;
                }
                if (this.type == 3) { //
                    this.selectedObj.eqValue = this.selectedObj.allEq - this.selectedObj.loseEq;
                }
                if (this.type == 6 || this.type == 7) { //
                    this.getContractById(val.id)
                }
            },
            // id
            getContractById(billId) {
                getExcepContractQuery({
                    billId,
                }).then(res => {
                    this.contract = res.data ? res.data : {};
                    //  
                    if (res.data && this.selectedObj.estimatePrice && this.selectedObj.estimatePrice != this
                        .contract
                        .price) {
                        this.selectedObj.tempPrice = this.selectedObj.estimatePrice;
                    } else {
                        this.selectedObj.tempPrice = '';
                    }
                })
            },
            changeMonth(t) {
                return parseTime(new Date(t), '{y}{m}')
            },
            handleClose() {
                this.$emit('exceptionHand')
            },
            //
            submit() {
                let opt = {};
                this.$refs['form'].validate((valid) => {
                    if (valid) {
                        if (this.allEqFP < 0 || this.calcAllEq < 0) {
                            this.$message({
                                type: 'warning',
                                message: '0',
                            })
                            return;
                        }
                        if (this.type == 1) { //
                            opt = {
                                id: this.selectedObj.id,
                                arEnum: this.selectedObj.arEnum * 1, //
                                arSnum: this.selectedObj.arSnum * 1, //
                                useEq: this.useEq * 1, //
                                loseEq: this.selectedObj.loseEq * 1, //
                                allEq: this.calcAllEq, //
                            }
                        } else if (this.type == 2) { //
                            if (!this.fileList.length) {
                                this.$message({
                                    type: 'warning',
                                    message: '',
                                })
                                return;
                            }
                            if (this.selectedObj.drEq > this.calcAllEq) {
                                this.$message({
                                    type: 'warning',
                                    message: '',
                                })
                                return;
                            }
                            opt = {
                                id: this.selectedObj.id,
                                arEnum: this.selectedObj.arEnum * 1, //
                                arSnum: this.selectedObj.arSnum * 1, //
                                allEq: this.calcAllEq, //
                                loseEq: this.selectedObj.loseEq * 1, //
                                useEq: this.useEq * 1, //
                                dayPaperFile: this.excelName ? this.excelName : 'excel',
                            }
                        } else if (this.type == 3) { // 
                            if (!this.filesFP.length) {
                                this.$message({
                                    type: 'warning',
                                    message: '',
                                })
                                return;
                            }
                            opt = {
                                id: this.selectedObj.id,
                                allEq: this.allEqFP, //
                                loseEq: this.selectedObj.loseEq * 1, //
                                stateGridPaper: this._getUrl(this.filesFP[0])
                            }
                        } else if (this.type == 4) {
                            if (!this.filesFP.length) {
                                this.$message({
                                    type: 'warning',
                                    message: '',
                                })
                                return;
                            }
                            opt = { //+
                                id: this.selectedObj.id,
                                arEnum: this.selectedObj.arEnum * 1, //
                                arSnum: this.selectedObj.arSnum * 1, //
                                useEq: this.useEq * 1, //
                                loseEq: this.selectedObj.loseEq * 1, //
                                allEq: this.calcAllEq, //
                                stateGridPaper: this._getUrl(this.filesFP[0])

                            }
                        } else if (this.type == 6 || this.type == 7) { //
                            opt = {
                                id: this.selectedObj.id,
                                price: this.selectedObj.tempPrice,
                            }
                        }
                        this.$emit('exceptionHand', opt)
                    } else {
                        return false;
                    }
                })
            },
            inputS(val) { //0
                if (~val.indexOf('0.0') && Number(val) == 0) return;
                this.selectedObj.arSnum = val < 0 ? 0 : Number(val);
                // 
                this.selectedObj.arEnum = Number(Number(this.selectedObj.arSnum + this.calc * 1).toFixed(5));
            },
            inputE(val) {
                this.selectedObj.arEnum = Number(this.selectedObj.arEnum) < Number(this.selectedObj.arSnum) ? this
                    .selectedObj.arSnum :
                    Number(this.selectedObj.arEnum)
                this.$refs['form'].validateField('arEnum');
            },
            // inputSumber(val) {

            // },

            //
            beforeUpload(file) {
                const name = file.name.toLowerCase();
                const isExcel = ['.xls', '.xlsx'].find(item => {
                    return name.indexOf(item) !== -1;
                })
                const isLt2M = file.size / 1024 / 1024 < 2;
                if (!isExcel) {
                    this.$message.error('excel!');
                    return false;
                }
                if (!isLt2M) {
                    this.$message.error(' 2MB!');
                    return false;
                }
                this.upLoading = true;
                this.excelName = name; //excel
                return true;
            },
            //
            beforeUploadFP(file) {
                if (this.$refs.uploadPreogress.isLoading()) return false;
                const name = file.name.toLowerCase();
                const isPngOrPdf = ['.png', '.jpeg', '.jpg', '.pdf'].find(item => {
                    return name.indexOf(item) !== -1;
                })
                const isLt2M = (file.size / 1024 / 1024) < 2;
                if (!isPngOrPdf) {
                    this.$message.error('pngjpegjpgpdf!');
                    return false;
                }

                this.$refs.uploadPreogress.start(); //

                //2M 
                if (!isLt2M && !~name.indexOf('.pdf')) {
                    return tool.compressImg(file);
                } else {
                    return true;
                }
            },
            //url
            _getUrl(item) {
                if (!item) return '';
                if (item.response) {
                    return item.response.fileName;
                } else if (item.url) {
                    return item.url.replace(baseProfileUrl, '');
                }
                return '';
            },
            //
            _getFileNameByUrl(url) {
                if (!url) return '';
                const _index = url.indexOf('_');
                return _index !== -1 ? url.slice(_index + 1) : url;
            },
            // 
            handleChange(res, file, fileList) {
                this.upLoading = false;
                if (res.code == 200) {
                    this.fileList = fileList;
                    this.selectedObj.drEq = res.data.useEq;
                } else {
                    this.msg(res.msg);
                    this.fileList = [];
                }
                if (this.fileList.length >= 2) {
                    this.fileList = [this.fileList[1]];
                }
            },
            // 
            handleChangeFP(res, file, fileList) {
                if (res.code == 200) {
                    this.filesFP = fileList.slice(0);
                    this.$refs.uploadPreogress.success();
                } else {
                    this.msg(res.msg);
                    this.filesFP = [];
                }
            },
            //svg
            getPreType(file) {
                const name = file.name.toLowerCase();
                const baseImg = ''
                if (name.indexOf('.doc') !== -1 || name.indexOf('.docx') !== -1) { // word
                    return 'w'
                } else if (name.indexOf('.pdf') !== -1) {
                    return 'pdf'
                } else if (name.indexOf('.ppt') !== -1 || name.indexOf('.pptx') !== -1) {
                    return 'ppt'
                } else if (name.indexOf('.xls') !== -1 || name.indexOf('.xlsx') !== -1) {
                    return 's'
                }
                return 'zip';
            },
            //
            isImg(file) {
                const name = file.name.toLowerCase();
                return ['.png', '.jpeg', '.jpg'].find(item => {
                    return name.indexOf(item) !== -1;
                })
            },
            //
            handlePictureCardPreview(file) {
                this.$viewerApi({
                    images: [file.url],
                })
            },
            //
            handleRemove(data, file) {
                let index = 0;
                data.map((item, index) => {
                    if (item.name === file.name) {
                        index = index;
                    }
                })
                data.splice(index, 1)
            },
            //
            getShowUrl(url) {
                if (!url) return '';
                return baseProfileUrl + url;
            },
            goRouter() {
                this.handleClose();
                this.$router.push("/useEqManage");
            },
            error() {
                this.upLoading = false;
            },
            msg(err) {
                this.$message.error(':' + err);
            },
        },
        watch: {
            detailData: {
                handler(val) {
                    if (!val) return;
                    this.initPage(val);
                },
                deep: true,
                immediate: true,
            },
        }
    }
</script>

<style scoped lang="scss">
    .form {
        .li {
            &:last-child {
                margin-bottom: 0;
            }

            .link {
                cursor: pointer;
                color: #0628f7;
                text-decoration: underline;
            }

            .box {
                &.contract {
                    padding: 16px 10px 16px 22px;
                    background-color: rgb(230, 234, 254);
                }

                .li {
                    display: flex;
                    justify-content: space-between;
