import {
    request
} from '../utils/request'
export function getBills(query) { //
    return request({
        method: 'get',
        url: `dl/bill/`,
        params: query
    })
}
export function billDetail(id) { //
    return request({
        method: 'get',
        url: `dl/bill/get/` + id,
    })
}

export function sureBill(data) { //
    return request({
        method: 'post',
        url: `dl/bill/confirm`,
        data
    })
}



export function innerBills(query) { //
    return request({
        method: 'get',
        url: `dl/bill/comfirm/list/innerBills`,
        params: query
    })
}
