<!---->
<template>
    <div>
        <SearchInputs @downHand="downHand" @searchHand="searchHand"></SearchInputs>
        <CusTable :tableData="tableData" @btnClick="btnClick" @handleSizeChange="handleSizeChange"
            @handleCurrentChange="handleCurrentChange" :pageNum="page.pageNum" :pageSize="page.pageSize" :total="total">
        </CusTable>
        <!-- -->
        <el-dialog :title="tableDialogType==1?'':''" custom-class="cusDialog"
            :visible.sync="showTableDialog" width="10rem" :before-close="handleClose" @closed="closed">
            <!--  -->
            <el-dialog width="6.8rem" title="" :visible.sync="showTableFlowDialog" append-to-body>
                <BillFlow :id="billId" :sitePaperFile="detailData.sitePaperFile"></BillFlow>
                <span slot="footer" class="dialog-footer">
                    <el-button @click="closeFlowDialog"> </el-button>
                </span>
            </el-dialog>
            <!--  -->
            <div class="print-body">
                <TempalteBill v-if="showPrintWd&&tableDialogType==1" :type="4" :tableData="detailData"></TempalteBill>
                <TempalteBill2 v-if="showPrintWd&&tableDialogType==2" :type="1" :tableData="detailData"></TempalteBill2>
            </div>
            <span slot="footer" class="dialog-footer">
                <el-button v-show="haveFp" @click="showFp"></el-button>
                <el-button type="primary" @click="html2Excel"> </el-button>
                <el-button type="primary" v-print="printObj"> </el-button>
                <el-button @click="showBillFlow"></el-button>
                <el-button @click="handleClose"> </el-button>
            </span>
        </el-dialog>
        <!---->
        <ConfirmModel :show="confirmModelVisible" :detailData="detailData" :loading="loading"
            @confirmHand="confirmHand">
        </ConfirmModel>
    </div>
</template>
<script>
    //
    import TableCommon from "@/components/TableCommon.vue";
    import CusTable from "./components/userComfirm/BillTable.vue";
    import SearchInputs from "./components/userComfirm/SearchInputs.vue";
    import BillFlow from "./components/common/BillFlow.vue";
    import TempalteBill from "@/components/tempalteBill.vue";
    import ConfirmModel from "./components/userComfirm/ConfirmModel.vue";
    import {
        parseTime
    } from "@/utils/index";
    //
    import {
        getUserConfirmBill, //
        postBillWithdraw, //
        exportBill, //
        postImportSite, //
    } from "@/api/archives.js";
    import {
        baseProfileUrl
    } from '@/api/baseUrl.js';
    //
    import tool from "@/utils/tool";
    import {
        mapState
    } from 'vuex';
    import Table2Excel from 'table2excel.js'
    export default {
        extends: TableCommon,
        name: 'UserComfirm',
        components: {
            CusTable,
            SearchInputs,
            BillFlow, //
            TempalteBill, //
            ConfirmModel,
            TempalteBill2: TempalteBill, //
        },
        data() {
            return {
                examModelVisible: false, //
                errorModelVisible: false, //
                dataInputModelVisible: false, //

                detailData: {}, //

                showTableDialog: false, //
                showTableFlowDialog: false, //

                billId: '', //id
                tableDialogType: 1, //
                showPrintWd: false, //
                printObj: { //
                    id: "printArea",
                    popTitle: '',
                },
                haveFp: false, //
                previewShow: false, //

                confirmModelVisible: false, //
            }
        },
        computed: {
            ...mapState({
                userId: state => state.user.userId, //user
            })
        },
        mounted() {},
        methods: {
            parseTime,
            isYes: tool.isYes,
            _getData(params = {}) { //    
                params = {
                    ...this.search,
                    ...this.page,
                }
                getUserConfirmBill(params).then(res => {
                    const data = res.rows ? res.rows : [];
                    //
                    data.forEach((item, index) => {
                        item.index = (this.page.pageNum - 1) * this.page.pageSize + index + 1;
                        if (item.type == 4) { // 
                            item.index += ' ';
                        }
                        if (item.subBills && item.subBills.length) {
                            item.subBills.forEach((p, k) => {
                                p.index = item.index + '.' + (k + 1);
                                p.isChild = true;
                            })
                        }
                    });
                    this.tableData = data;
                    this.total = res.total;
                })
            },
            showFp() { //
                let previewUrl = baseProfileUrl + this.detailData.stateGridPaper;
                if (!this.detailData.stateGridPaper) {
                    this.$message.warning('')
                    return;
                }
                this.$viewerApi({
                    images: [previewUrl],
                })
            },
            //excel
            html2Excel() {
                const name = this.detailData.kwell ? this.detailData.kwell : this.detailData.kpName;
                new Table2Excel('#printArea table').export(
                    `${name}-${this.detailData.month}-${this.tableDialogType==1?'':''}`);
            },
            //
            downHand() {
                exportBill({
                    billState: '7',
                    ...this.search,
                }).then(tool.down)
            },

            btnClick(name, row) {
                this.detailData = row;
                this.haveFp = this.isYes(row.stateGirdPaperImp);

                if (name == "") {
                    this.showTableDialog = true;
                    this.tableDialogType = 1; //
                    this.showPrintWd = true;
                } else if (name == "") {
                    this.showTableDialog = true;
                    this.tableDialogType = 2; //
                    this.showPrintWd = true;
                } else if (name == "") {
                    postBillWithdraw({
                        billId: row.id,
                        date: this.parseTime(new Date()),
                        solveType: "7",
                        userId: this.userId,
                        reason: row.reason
                    }).then(res => {
                        this.resPublick(res);
                    })
                } else if (name == "") { //
                    this.confirmModelVisible = true;
                }
            },
            confirmHand(data) {
                if (!data) {
                    this.confirmModelVisible = false;
                    return;
                }
                postImportSite(data).then(res => {
                    this.resPublick(res);
                    this.confirmModelVisible = false;
                })
            },
            handleClose() {
                this.dialogShow = false;
            },
            //
            resPublick(res) {
                this.loading = false; //
                this.$message({
                    type: 'success',
                    message: ''
                })
                this.reloadTable();
            },
            closed() {
                this.showPrintWd = false;
            },
            //
            showBillFlow() {
                this.billId = this.detailData.id;
                this.showTableFlowDialog = true;
            },
            handleClose() {
                this.showTableDialog = false;
            },
            closeFlowDialog() {
                this.showTableFlowDialog = false;
            },
            //word
            html2word() {
                new Table2Excel('#printArea table').export()
            },
        }
    }
</script>