<template>
    <div>
        <!---->
        <el-dialog title="" custom-class="cusDialog" :visible.sync="show" width="6rem"
            :before-close="handleClose">
            <el-form :model="data" ref="form" class="form">
                <div class="li">
                    <div class="item">
                        <div class="label"></div>
                        <div class='rt'>
                            <div class="box">{{detailData.kcName}}</div>
                        </div>
                    </div>
                </div>
                <div class="li">
                    <div class="item">
                        <div class="label"></div>
                        <div class='rt'>
                            <div class="box">{{detailData.kpName}}</div>
                        </div>
                    </div>
                </div>
                <div class="li">
                    <div class="item">
                        <div class="label"></div>
                        <div class='rt'>
                            <div class="box">{{detailData.sdName}}</div>
                        </div>
                    </div>
                </div>
                <div class="li">
                    <div class="item">
                        <div class="label"></div>
                        <div class='rt'>
                            <div class="box">{{ parseTime(new Date(detailData.month),'{y}{m}')}}
                            </div>
                        </div>
                    </div>
                </div>
                <div class="li">
                    <div class="item">
                        <div class="label"></div>
                        <div class='rt'>
                            <div class="box">
                                {{parseTime(new Date(detailData.arSt),'{y}/{m}/{d} {h}:{i}')}}-
                                {{parseTime(new Date(detailData.arEt),'{y}/{m}/{d} {h}:{i}')}}
                            </div>
                        </div>
                    </div>
                </div>
                <div class="li">
                    <div class="item">
                        <div class="label"></div>
                        <div class='rt'>
                            <div class="box">{{detailData.allEq}} kWh</div>
                        </div>
                    </div>
                </div>
                <div class="li">
                    <div class="item">
                        <div class="label"><span class="important">*</span></div>
                        <div class='rt'>
                            <el-form-item>
                                <el-select v-model="data.remark" placeholder="">
                                    <el-option label="" value="" :key="0"></el-option>
                                    <el-option label="" value="" :key="1"></el-option>
                                    <el-option label="" value="" :key="2"></el-option>
                                    <el-option label="" value="" :key="3"></el-option>
                                </el-select>
                            </el-form-item>
                        </div>
                    </div>
                </div>
                <div class="li">
                    <div class="item">
                        <el-form-item>
                            <div class="label"><span class="important">*</span></div>
                            <div class='rt'>
                                <el-upload :class="{hide:files.length}" :action="fileUploadUrl" :headers="headers"
                                    :on-success="handleChange" :before-upload="beforeUploadFP" list-type="picture-card"
                                    :limit="1" :file-list="files">
                                    <i slot="default" class="el-icon-plus"></i>
                                    <div slot="file" slot-scope="{file}">
                                        <img v-if="isImg(file)" class="el-upload-list__item-thumbnail" :src="file.url"
                                            alt="">
                                        <div v-else class="box">
                                            <svg-icon :icon-class="getPreType(file)" class-name="logo-img">
                                            </svg-icon>
                                        </div>
                                        <span class="el-upload-list__item-actions">
                                            <span v-if="isImg(file)" class="el-upload-list__item-preview"
                                                @click="handlePictureCardPreview(file)">
                                                <i class="el-icon-zoom-in"></i>
                                            </span>
                                            <span class="el-upload-list__item-delete" @click="handleRemove(files,file)">
                                                <i class="el-icon-delete"></i>
                                            </span>
                                        </span>
                                    </div>
                                </el-upload>
                                <p></p>
                                <UploadProgress ref="uploadPreogress" />
                            </div>
                        </el-form-item>
                    </div>
                </div>

            </el-form>
            <span slot="footer" class="dialog-footer">
                <el-button type="primary" :loading="loading" @click="submit"> </el-button>
                <el-button @click="handleClose"> </el-button>
            </span>
        </el-dialog>
    </div>
</template>
<script>
    import {
        Message
    } from 'element-ui';
    import {
        getToken
    } from '@/utils/auth'
    import {
        mapState
    } from 'vuex';
    import {
        fileUploadUrl,
        resourceUrl,
    } from '@/api/archives';
    import tool from '@/utils/tool'
    import {
        parseTime
    } from "@/utils/index";
    import UploadProgress from "@/components/UploadProgress.vue"; //
    export default {
        components: {
            UploadProgress,
        },
        props: {
            show: {
                type: Boolean,
                default: false,
            },
            detailData: {
                type: Object,
                default: () => {},
            },
            loading: Boolean
        },
        data() {
            return {
                fileUploadUrl, //,
                files: [], //
                headers: {
                    uapAccessToken: getToken('accessToken'),
                    uapRefreshToken: getToken('refreshToken'),
                },
                fileUploadUrlGw: '', //
                data: {
                    remark: ''
                },
            }
        },
        computed: {
            ...mapState({
                userId: state => state.user.userId, //user
            })
        },
        methods: {
            parseTime,
            //
            handleChange(res, file, fileList) {
                if (res.code == 200) {
                    this.files = fileList.slice(0);
                    this.$refs.uploadPreogress.success();
                } else {
                    this.msg(res.msg);
                    this.files = [];
                }
            },
            isImg(file) {
                const name = file.name.toLowerCase();
                return ['.png', '.jpeg', '.jpg'].find(item => {
                    return name.indexOf(item) !== -1;
                })
            },
            //
            handleRemove(data, file) {
                let index = 0;
                data.map((item, index) => {
                    if (item.name === file.name) {
                        index = index;
                    }
                })
                data.splice(index, 1)
            },
            //
            handlePictureCardPreview(file) {
                this.$viewerApi({
                    images: [file.url],
                })

            },
            //url
            _getUrl(item) {
                if (!item) return '';
                if (item.response) {
                    return item.response.fileName;
                } else if (item.url) {
                    return item.url.replace(baseProfileUrl, '');
                }
                return '';
            },
            //
            beforeUploadFP(file) {
                if (this.$refs.uploadPreogress.isLoading()) return false;
                const name = file.name.toLowerCase();
                const isPngOrPdf = ['.png', '.jpeg', '.jpg', '.pdf'].find(item => {
                    return name.indexOf(item) !== -1;
                })
                const isLt2M = (file.size / 1024 / 1024) < 2;
                if (!isPngOrPdf) {
                    this.$message.error('pngjpegjpgpdf!');
                    return false;
                }

                this.$refs.uploadPreogress.start(); //

                //2M 
                if (!isLt2M && !~name.indexOf('.pdf')) {
                    return tool.compressImg(file);
                } else {
                    return true;
                }
            },
            handleClose() {
                this.$emit('confirmHand')
            },
            submit() {
                if (!this.files[0]) {
                    if (!this.$refs.uploadPreogress.isLoading()) { //
                        this.$message.warning('');
                    } else { //
                        this.$message.warning('');
                    }
                    return;
                }
                this.$emit('confirmHand', {
                    id: this.detailData.id,
                    userId: this.userId,
                    remark: this.data.remark,
                    sitePaperFile: this._getUrl(this.files[0]),
                    parentId: this.detailData.parentId
                })
                //
                this.data.remark = "";
                this.files = []

            }
        },
    }
</script>

<style scoped lang="scss">
    .form {
        .li {
            &:last-child {
                margin-bottom: 0;
            }

            /deep/ .el-select {
                width: 100%;
            }

            display: flex;
            margin-bottom: 22px;

            .item {
                width: 100%;
                display: flex;
                line-height: 32px;
                height: 32;

                .label {
                    font-family: "PFC";
                    min-width: 0.5rem;
                    margin-right: 2px;
                    color: #333;
                    text-align: right;
                }

                .box {
                    width: 100%;
                    border-radius: 4px;
                    color: #666;
                    padding: 0 10px;
                    box-sizing: border-box;
                }

                .rt {
                    flex: 1;

                    /deep/ .el-form-item {
                        margin-bottom: 0
                    }
                }

                .btns {
                    width: 100%;
                    display: flex;
                    justify-content: flex-end;

                }
            }
        }

        /deep/ .hide .el-upload--picture-card {
            display: none;
        }
    }
</style>