<!---->
<template>
    <div>
        <SearchInputs @downHand="downHand" @searchHand="searchHand"></SearchInputs>
        <CusTable :tableData="tableData" @btnClick="btnClick" @handleSizeChange="handleSizeChange"
            @handleCurrentChange="handleCurrentChange" :pageNum="page.pageNum" :pageSize="page.pageSize" :total="total">
        </CusTable>

        <!---->
        <ErrorDlModel :show="this.errorModelVisible" :detailData="detailData" :loading="loading" @errorHand="errorHand"
            :type="2">
        </ErrorDlModel>

        <!-- -->
        <el-dialog title="" custom-class="cusDialog" :visible.sync="showTableDialog" width="10rem"
            :before-close="handleClose" @closed="closed">
            <!--  -->
            <el-dialog width="6.8rem" title="" :visible.sync="showTableFlowDialog" append-to-body>
                <BillFlow :id="billId" :sitePaperFile="detailData.sitePaperFile"></BillFlow>
                <span slot="footer" class="dialog-footer">
                    <el-button @click="closeFlowDialog"> </el-button>
                </span>
            </el-dialog>
            <!--  -->

            <!---->
            <div class="print-body">
                <TempalteBill v-if="showPrintWd" :type="1" :tableData="detailData"></TempalteBill>
            </div>
            <span slot="footer" class="dialog-footer">
                <el-button v-show="haveFp" @click="showFp"></el-button>
                <el-button type="primary" @click="html2Excel"> </el-button>
                <el-button type="primary" v-print="printObj"> </el-button>
                <el-button @click="showBillFlow"></el-button>
                <el-button @click="handleClose"> </el-button>
            </span>
        </el-dialog>
    </div>
</template>
<script>
    //
    import TableCommon from "@/components/TableCommon.vue";
    import CusTable from "./components/marketComfirm/BillTable.vue";
    import SearchInputs from "./components/billCreate/SearchInputsCreate.vue";
    import ErrorDlModel from "./components/common/ErrorDlModel.vue";
    import BillFlow from "./components/common/BillFlow.vue";
    import TempalteBill from "@/components/tempalteBill.vue";
    import {
        parseTime
    } from "@/utils/index";
    //
    import {
        getMarketerConfirm, //
        postBillWithdraw, //
        postBillException, //
        exportBill,
    } from "@/api/archives.js";
    import {
        baseProfileUrl
    } from '@/api/baseUrl.js';
    //
    import tool from "@/utils/tool";
    import Table2Excel from 'table2excel.js'
    import {
        mapState
    } from 'vuex';

    export default {
        extends: TableCommon,
        name: 'MarketComfirm',
        components: {
            CusTable,
            SearchInputs,
            ErrorDlModel, //
            BillFlow, //
            TempalteBill, //
        },
        data() {
            return {
                errorModelVisible: false, //

                showTableDialog: false, //
                showTableFlowDialog: false, //
                detailData: {}, //

                billId: '', //id
                showPrintWd: false, //
                tab: 1,

                printObj: { //
                    id: "printArea",
                    popTitle: '',
                },
                haveFp: false, //
            }
        },
        computed: {
            ...mapState({
                userId: state => state.user.userId, //user
            })
        },
        mounted() {},
        methods: {
            isYes: tool.isYes,
            parseTime,
            _getData(params = {}) { //    
                params = {
                    ...this.search,
                    ...this.page,
                }
                getMarketerConfirm(params).then(res => {
                    const data = res.rows ? res.rows : [];
                    //
                    data.forEach((item, index) => {
                        item.index = (this.page.pageNum - 1) * this.page.pageSize + index + 1;
                        if (item.type == 4) { // 
                            item.index += ' ';
                        }
                        if (item.subBills && item.subBills.length) {
                            item.subBills.forEach((p, k) => {
                                p.index = item.index + '.' + (k + 1);
                                p.isChild = true;
                            })
                        }
                    });
                    this.tableData = data;
                    this.total = res.total;
                })
            },
            titleTab(tab) {
                this.tab = tab;
            },
            //excel
            html2Excel() {
                const name = this.detailData.kwell ? this.detailData.kwell : this.detailData.kpName;
                new Table2Excel('#printArea table').export(`${name}-${this.detailData.month}-`);
            },
            showFp() { //
                let previewUrl = baseProfileUrl + this.detailData.stateGridPaper;
                if (!this.detailData.stateGridPaper) {
                    this.$message.warning('')
                    return;
                }
                this.$viewerApi({
                    images: [previewUrl],
                })
            },
            //
            downHand() {
                exportBill({
                    billState: '4',
                    ...this.search,
                }).then(tool.down)
            },
            //
            showBillFlow() {
                this.billId = this.detailData.id;
                this.showTableFlowDialog = true;
            },
            handleClose() {
                this.showTableDialog = false;
            },
            closeFlowDialog() {
                this.showTableFlowDialog = false;
            },
            //
            errorHand(row, data) {
                if (!row) {
                    this.errorModelVisible = false;
                    return;
                }
                this.loading = true;
                postBillException({
                    billId: row.id,
                    userId: this.userId,
                    solveType: data.solveType,
                    reason: data.reason,
                    date: this.parseTime(new Date()),
                }).then(res => {
                    this.loading = false; //
                    this.errorModelVisible = false; //
                    this.resPublick(res);
                })
            },
            btnClick(name, row) {
                this.detailData = row;
                this.haveFp = this.isYes(row.stateGirdPaperImp);
                if (name == "") {
                    this.showPrintWd = true;
                    this.showTableDialog = true;
                } else if (name == "") {
                    this.errorModelVisible = true;
                }
            },

            //
            closed() {
                this.showPrintWd = false;
            },
            //
            resPublick(res) {
                this.loading = false; //
                this.$message({
                    type: 'success',
                    message: ''
                })
                this.reloadTable();
            },
        }
    }
</script>

<style scoped lang="scss">
    .cus-title {
        display: flex;

        .title-btn {
            &.active {
                color: #2E4AF7;
            }

            cursor: pointer;
            margin-right: 60px;
            font-weight: 500;
            color: #333333;
            line-height: 20px;
            font-family: "PFC"
        }
    }
</style>