<template>
    <!---->
    <div>
        <el-dialog :title="type==1?'':'-'" custom-class="cusDialog" :visible.sync="show" width="6rem"
            :before-close="handleClose">
            <div class="form">
                <div class="li" v-if="type==1">
                    <div class="item">
                        <div class="label"></div>
                        <div class='rt'>
                            <el-input v-model="otherName" placeholder="">
                                <template slot="prepend">{{formData.kpName}}</template>
                            </el-input>
                        </div>
                    </div>
                </div>
                <template v-if="type==2">
                    <div class="li">
                        <div class="item">
                            <div class="label"></div>
                            <div class='rt'>
                                <div class="box">{{formData.kpName}}</div>
                            </div>
                        </div>
                    </div>
                    <div class="li">
                        <div class="item">
                            <div class="label"></div>
                            <div class='rt'>
                                <div class="box">{{formData.sdName}}</div>
                            </div>
                        </div>
                    </div>
                    <div class="li">
                        <div class="item">
                            <div class="label"></div>
                            <div class='rt'>
                                <div class="box">{{formData.workType}}</div>
                            </div>
                        </div>
                    </div>
                    <div class="li">
                        <div class="item">
                            <div class="label"></div>
                            <div class='rt'>
                                <div class="box">{{changeMonth(formData.month)}}</div>
                            </div>
                        </div>
                    </div>

                    <div v-if="showStAndEtInput" class="li">
                        <div class="item">
                            <div class="label"><span class="important">*</span></div>
                            <div class='rt'>
                                <el-input v-model="formData.arSnum" type="number" min="0" placeholder="">
                                </el-input>
                            </div>
                        </div>
                    </div>
                    <div v-if="showStAndEtInput" class="li">
                        <div class="item">
                            <div class="label"><span class="important">*</span></div>
                            <div class='rt'>
                                <el-input v-model="formData.arEnum" type="number" min="0" placeholder="">

                                </el-input>
                            </div>
                        </div>
                    </div>
                    <div v-if="showStAndEtInput" class="li">
                        <div class="item">
                            <div class="label"><span class="important">*</span></div>
                            <div class='rt'>
                                <el-input v-model="formData.loseEq" type="number" min="0" placeholder="">
                                    <template slot="append">kWh</template>
                                </el-input>
                            </div>
                        </div>
                    </div>
                    <div v-if="showStAndEtInput" class="li">
                        <div class="item">
                            <div class="label"></div>
                            <div class='rt'>
                                <div class="box">{{formData.blRate}}</div>
                            </div>
                        </div>
                    </div>

                    <div v-if="showStAndEtInput" class="li">
                        <div class="item">
                            <div class="label"></div>
                            <div class='rt'>
                                <div class="box">{{calcAllEq}}</div>
                            </div>
                        </div>
                    </div>
                    <div v-else class="li">
                        <div class="item">
                            <div class="label"><span class="important">*</span></div>
                            <div class='rt'>
                                <el-input v-model="formData.allEq" type="number" min="0" placeholder="">
                                    <template slot="append">kWh</template>
                                </el-input>
                            </div>
                        </div>
                    </div>

                    <div class="li" v-if="this.isYes(this.formData.stateGirdPaperImp)">
                        <div class="item">
                            <div class="label"><span class="important">*</span></div>
                            <div class='rt'>
                                <el-upload :class="{hide:files.length}" :action="fileUploadUrl" :headers="headers"
                                    :on-success="handleChange" :before-upload="beforeUploadFP" list-type="picture-card"
                                    :limit="2" :file-list="files">
                                    <i slot="default" class="el-icon-plus"></i>
                                    <div slot="file" slot-scope="{file}">
                                        <img v-if="isImg(file)" class="el-upload-list__item-thumbnail" :src="file.url"
                                            alt="">
                                        <div v-else class="box">
                                            <svg-icon :icon-class="getPreType(file)" class-name="logo-img">
                                            </svg-icon>
                                        </div>
                                        <span class="el-upload-list__item-actions">
                                            <span v-if="isImg(file)" class="el-upload-list__item-preview"
                                                @click="handlePictureCardPreview(file)">
                                                <i class="el-icon-zoom-in"></i>
                                            </span>
                                            <span class="el-upload-list__item-delete" @click="handleRemove(files,file)">
                                                <i class="el-icon-delete"></i>
                                            </span>
                                        </span>
                                    </div>
                                </el-upload>
                                <UploadProgress ref="uploadPreogress" />
                            </div>
                        </div>
                    </div>
                    <div class="li" v-if="this.isYes(this.formData.dayPaperImp)">
                        <div class="item">
                            <div class="label"><span class="important">*</span></div>
                            <div class='rt'>
                                <el-upload class="upload-demo" :action="fileUploadUrlGw" :headers="headers"
                                    :on-success="handleChangeDetail" :before-upload="beforeUpload" :on-error="error"
                                    :data="params" :show-file-list="false">
                                    <span class="fileBtn">{{fileList.length?fileList[0].name:''}}</span>
                                </el-upload>
                            </div>
                        </div>
                    </div>
                </template>
            </div>
            <span slot="footer" class="dialog-footer">
                <el-button type="primary" :loading="loading" @click="submit"> </el-button>
                <el-button @click="handleClose"> </el-button>
            </span>
        </el-dialog>
    </div>
</template>
<script>
    import {
        parseTime,
        formatNum
    } from "@/utils/index";
    import {
        fileUploadUrl,
        postStateGridFile,
        resourceUrl,
        billExport
    } from '@/api/archives';
    import {
        baseUrl,
        baseProfileUrl
    } from '@/api/baseUrl.js';
    import {
        getToken
    } from '@/utils/auth'
    import tool from '@/utils/tool'

    import UploadProgress from "@/components/UploadProgress.vue"; //
    export default {
        components: {
            UploadProgress,
        },
        props: {
            show: {
                type: Boolean,
                default: false,
            },
            type: { // 1 2()
                type: Number,
                default: 1,
            },
            detailData: {
                type: Object,
                default: () => {},
            },
            loading: Boolean
        },
        data() {
            return {
                showStAndEtInput: false,
                formData: {
                    kwell: '1', //
                    kpName: '', //
                    type: '', //
                    //
                    workType: '', //
                    sdName: '', //
                    month: '', // 
                    allEq: '', // 
                    loseEq: 0,
                    arSnum: 0,
                    arEnum: 0,
                    uploadEq: '', // 
                },
                fileUploadUrl, //,
                files: [], //
                fileList: [], //
                headers: {
                    uapAccessToken: getToken('accessToken'),
                    uapRefreshToken: getToken('refreshToken'),
                },
                fileUploadUrlGw: '', //
                params: { //
                    id: ''
                },
                excelName: '', //excel
                upLoading: false,
                otherName: '',
            }
        },
        mounted() {
            this.detailData && this.init(this.detailData);
        },
        computed: {
            //=-* + 
            calcAllEq() {
                const sum = ((this.formData.arEnum - this.formData.arSnum).toFixed(6) * this.formData.blRate)
                    .toFixed(2);
                let calc = formatNum(sum) < 0 ? 0 : formatNum(sum);
                return formatNum(calc + Number(this.formData.loseEq))
            }
        },
        methods: {
            isYes: tool.isYes,
            init(val) { //
                this.formData.id = val.id;
                this.formData.kwell = val.kwell;
                this.formData.kpName = val.kpName;
                this.formData.workType = val.workType;
                this.formData.sdName = val.sdName;
                this.formData.month = val.month;
                this.formData.allEq = val.allEq;
                this.formData.type = val.type;

                this.formData.arSnum = val.arSnum ? val.arSnum : 0;
                this.formData.arEnum = val.arEnum ? val.arEnum : 0;
                this.formData.blRate = val.blRate ? val.blRate : 0;
                this.formData.loseEq = val.loseEq ? val.loseEq : 0;

                this.formData.stateGridPaper = val.stateGridPaper;
                //
                // this.formData.dayPaperImp = 'Y'; //
                // this.formData.stateGirdPaperImp = 'N'; //
                // val.kcCode = 'KC0001'

                this.formData.dayPaperImp = val.dayPaperImp; //
                this.formData.stateGirdPaperImp = val.stateGirdPaperImp; //

                this.otherName = val.kwell ? val.kwell.replace(val.kpName, '') : '';
                this.params.id = val.id;
                this.fileUploadUrlGw = billExport(this.formData.id);

                //   
                this.showStAndEtInput = tool.showStAndEtFunc(val.kcCode)

                //
                if (val.allEq && this.isYes(this.formData.dayPaperImp)) {
                    if (val.dayPaper) {
                        this.fileList = [{
                            url: '',
                            name: val.dayPaper,
                        }]
                    } else {
                        this.fileList = []
                    }
                    this.formData.uploadEq = val.allEq;
                } else if (val.allEq) { //
                    if (val.stateGridPaper) {
                        this.files = [{
                            url: this.getShowUrl(val.stateGridPaper),
                            name: this._getFileNameByUrl(val.stateGridPaper),
                        }];
                    } else {
                        this.files = [];
                    }
                    this.formData.uploadEq = val.allEq;
                } else {
                    this.files = [];
                    this.fileList = [];
                    this.formData.uploadEq = 0;
                }
                this.$refs.uploadPreogress && this.$refs.uploadPreogress.init();
            },
            //
            _getFileNameByUrl(url) {
                if (!url) return '';
                const _index = url.indexOf('_');
                return _index !== -1 ? url.slice(_index + 1) : url;
            },
            handleClose() {
                this.$emit('editHand');
            },
            //
            downloadExcel(item) {
                const url = item.url;
                const fileName = item.name;
                const a = document.createElement('a');
                a.setAttribute('download', fileName);
                a.setAttribute('href', resourceUrl + "?resource=" + url.replace(baseProfileUrl, ''));
                a.click();
            },
            submit() {
                let opt = {};
                if (this.type == 1) { //
                    opt = {
                        kwell: this.formData.kpName + this.otherName,
                        id: this.formData.id
                    }
                    this.$emit('editHand', opt, this.type);
                    return;
                }
                //
                //
                if (this.isYes(this.formData.stateGirdPaperImp)) {
                    if (!this.files[0]) {
                        if (!this.$refs.uploadPreogress.isLoading()) { //
                            this.$message.warning('');
                        } else { //
                            this.$message.warning('');
                        }
                        return;
                    }
                    opt.filePath = this._getUrl(this.files[0]);
                }
                // 
                if (this.isYes(this.formData.dayPaperImp)) {
                    if (this.formData.allEq <= 0) {
                        this.$message.warning('');
                        return;
                    }
                    if (!this.fileList.length) {
                        this.$message.warning('');
                        return;
                    }
                    if (this.formData.allEq != this.formData.uploadEq) {
                        this.$message.warning('');
                        return;
                    }
                }

                if (this.showStAndEtInput) { //
                    if (this.calcAllEq <= 0) {
                        this.$message.warning('');
                        return;
                    }
                    opt = {
                        ...opt,
                        arSnum: this.formData.arSnum,
                        arEnum: this.formData.arEnum,
                        loseEq: this.formData.loseEq,
                        useEq: formatNum(this.calcAllEq - this.formData.loseEq),
                        allEq: this.calcAllEq,
                        id: this.formData.id
                    }
                } else { // 
                    if (this.formData.allEq <= 0) {
                        this.$message.warning('');
                        return;
                    }
                    opt = {
                        ...opt,
                        allEq: this.formData.allEq,
                        id: this.formData.id
                    }
                }
                this.$emit('editHand', opt, this.type);
            },
            submitold() {
                let opt = '';
                if (this.type == 1) { //
                    opt = {
                        kwell: this.formData.kpName + this.otherName,
                        id: this.formData.id
                    }
                } else if (this.isYes(this.formData.stateGirdPaperImp)) { //
                    if (this.showStAndEtInput) { //
                        if (this.calcAllEq <= 0) {
                            this.$message.warning('');
                            return;
                        }
                    } else { // 
                        if (this.formData.allEq <= 0) {
                            this.$message.warning('');
                            return;
                        }
                    }

                    if (!this.files[0]) {
                        if (!this.$refs.uploadPreogress.isLoading()) { //
                            this.$message.warning('');
                        } else { //
                            this.$message.warning('');
                        }
                        return;
                    }
                    if (this.showStAndEtInput) { // 
                        opt = {
                            arSnum: this.formData.arSnum,
                            arEnum: this.formData.arEnum,
                            loseEq: this.formData.loseEq,
                            useEq: formatNum(this.calcAllEq - this.formData.loseEq),
                            allEq: this.calcAllEq,
                            filePath: this._getUrl(this.files[0]),
                            id: this.formData.id
                        }
                    } else {
                        opt = {
                            allEq: this.formData.allEq,
                            filePath: this._getUrl(this.files[0]),
                            id: this.formData.id
                        }
                    }
                } else if (this.formData.dayPaperImp) { // 
                    if (this.formData.allEq <= 0) {
                        this.$message.warning('');
                        return;
                    }
                    if (!this.fileList.length) {
                        this.$message.warning('');
                        return;
                    }
                    if (this.formData.allEq != this.formData.uploadEq) {
                        this.$message.warning('');
                        return;
                    }
                    opt = {
                        allEq: this.formData.allEq,
                        filePath: this.excelName,
                        id: this.formData.id
                    }
                } else { // 

                }
                this.$emit('editHand', opt, this.type);
            },
            changeMonth(t) {
                return parseTime(new Date(t), '{y}{m}')
            },
            //
            getShowUrl(url) {
                if (!url) return '';
                return baseProfileUrl + url;
            },
            //svg
            getPreType(file) {
                const name = file.name.toLowerCase();
                const baseImg = ''
                if (name.indexOf('.doc') !== -1 || name.indexOf('.docx') !== -1) { // word
                    return 'w'
                } else if (name.indexOf('.pdf') !== -1) {
                    return 'pdf'
                } else if (name.indexOf('.ppt') !== -1 || name.indexOf('.pptx') !== -1) {
                    return 'ppt'
                } else if (name.indexOf('.xls') !== -1 || name.indexOf('.xlsx') !== -1) {
                    return 's'
                }
                return 'zip';
            },
            isImg(file) {
                const name = file.name.toLowerCase();
                return ['.png', '.jpeg', '.jpg'].find(item => {
                    return name.indexOf(item) !== -1;
                })
            },
            //url
            _getUrl(item) {
                if (!item) return '';
                if (item.response) {
                    return item.response.fileName;
                } else if (item.url) {
                    return item.url.replace(baseProfileUrl, '');
                }
                return '';
            },
            //
            handlePictureCardPreview(file) {
                this.$viewerApi({
                    images: [file.url]
                })
            },
            //
            handleRemove(data, file) {
                let index = 0;
                data.map((item, index) => {
                    if (item.name === file.name) {
                        index = index;
                    }
                })
                data.splice(index, 1)
            },
            //
            handleChange(res, file, fileList) {
                if (res.code == 200) {
                    this.files = fileList.slice(0);
                    this.$refs.uploadPreogress.success();
                } else {
                    this.msg(res.msg);
                    this.files = [];
                }
            },
            // 
            handleChangeDetail(res, file, fileList) {
                this.upLoading = false;
                if (res.code == 200) {
                    this.fileList = fileList;
                    this.formData.uploadEq = res.data.useEq;
                    this.$message.success('');
                } else {
                    this.msg(res.msg);
                    this.fileList = [];
                }
                if (this.fileList.length >= 2) {
                    this.fileList = [this.fileList[1]];
                }
            },
            //
            beforeUpload(file) {
                const name = file.name.toLowerCase();
                const isExcel = ['.xls', '.xlsx'].find(item => {
                    return name.indexOf(item) !== -1;
                })
                const isLt2M = file.size / 1024 / 1024 < 2;
                if (!isExcel) {
                    this.$message.error('excel!');

                    return false;
                }
                if (!isLt2M) {
                    this.$message.error(' 2MB!');
                    return false;
                }
                this.upLoading = true;
                this.excelName = name; //excel
                return true;
            },
            //
            beforeUploadFP(file) {
                if (this.$refs.uploadPreogress.isLoading()) return false;
                const name = file.name.toLowerCase();
                const isPngOrPdf = ['.png', '.jpeg', '.jpg', '.pdf'].find(item => {
                    return name.indexOf(item) !== -1;
                })
                const isLt2M = (file.size / 1024 / 1024) < 2;
                if (!isPngOrPdf) {
                    this.$message.error('pngjpegjpgpdf!');
                    return false;
                }

                this.$refs.uploadPreogress.start(); //

                //2M 
                if (!isLt2M && !~name.indexOf('.pdf')) {
                    return tool.compressImg(file);
                } else {
                    return true;
                }
            },
            error() {
                this.upLoading = false;
            },
            msg(err) {
                this.$message.error(':' + err);
            },
        },
        watch: {
            detailData(val) {
                if (!val) return;
                this.init(val);
            },
            deep: true,
            immediate: true,
        }
    }
</script>

<style scoped lang="scss">
    .form {
        .li {
            display: flex;
            margin-bottom: 22px;



            .item {
                width: 100%;
                display: flex;
                line-height: 32px;
                height: 32;

                .label {
                    font-family: "PFC";
                    min-width: 0.8rem;
                    margin-right: 2px;
                    color: #333;
                    text-align: right;
                }

                .logo-img {
                    width: 95px;
                    height: 95px;
                    line-height: 0;
                    padding: 30px;
                    box-sizing: border-box;
                }

                .box {

                    width: 100%;
                    border-radius: 4px;
                    color: #666;

                    box-sizing: border-box;
                }

                .rt {
                    flex: 1;
                    padding: 0 10px;
                }

                .btns {
                    width: 100%;
                    display: flex;
                    justify-content: flex-end;

                }

                .fileBtn {
                    font-size: 14px;
                    color: #2E4AF7;
                    text-decoration: underline;
                }
            }
        }

        /deep/ .hide .el-upload--picture-card {
            display: none;
        }
    }
</style>