<!---->
<template>
    <div>
        <SearchInputs @downHand="downHand" @searchHand="searchHand"></SearchInputs>
        <CusTable :tableData="tableData" @btnClick="btnClick" @handleSizeChange="handleSizeChange"
            @handleCurrentChange="handleCurrentChange" :pageNum="page.pageNum" :pageSize="page.pageSize" :total="total">
        </CusTable>


        <!---->
        <SubTableDataInput :show="this.dataInputModelVisible" :detailData="detailData" :loading="loading"
            @dataInputHand="dataInputHand">
        </SubTableDataInput>

        <!-- -->
        <el-dialog :title="tableDialogType==1?'':''" custom-class="cusDialog"
            :visible.sync="showTableDialog" width="10rem" :before-close="handleClose" @closed="closed">
            <!--  -->
            <el-dialog width="6.8rem" title="" :visible.sync="showTableFlowDialog" append-to-body>
                <BillFlow :id="billId" :sitePaperFile="detailData.sitePaperFile"></BillFlow>
                <span slot="footer" class="dialog-footer">
                    <el-button @click="closeFlowDialog"> </el-button>
                </span>
            </el-dialog>

            <!--  -->
            <div class="print-body">
                <TempalteBill v-if="showPrintWd&&tableDialogType==1" :type="4" :tableData="detailData"></TempalteBill>
                <TempalteBill2 v-if="showPrintWd&&tableDialogType==2" :type="1" :tableData="detailData"></TempalteBill2>
            </div>
            <span slot="footer" class="dialog-footer">
                <el-button v-show="haveFp" @click="showFp"></el-button>
                <el-button type="primary" @click="html2Excel"> </el-button>
                <el-button type="primary" v-print="printObj"> </el-button>
                <el-button @click="showBillFlow"></el-button>
                <el-button @click="handleClose"> </el-button>
            </span>
        </el-dialog>
        <!---->
        <ExceptionDo :show="this.exceptionDoDialog" :detailData="detailData" @exceptionHand="exceptionHand"
            :type="solveType"></ExceptionDo>

        <!---->
        <SelectBill :show="this.selectBillDialog" :detailData="detailData" @selectHand="selectHand"></SelectBill>
    </div>
</template>
<script>
    //
    import TableCommon from "@/components/TableCommon.vue";
    import CusTable from "./components/exceptionList/BillTable.vue";
    import SearchInputs from "./components/billCreate/SearchInputsCreate.vue";
    import BillFlow from "./components/common/BillFlow.vue";
    import SubTableDataInput from "./components/exceptionList/SubTableDataInput.vue"; //
    import ExceptionDo from "./components/exceptionList/ExceptionDo.vue"; //
    import SelectBill from "./components/exceptionList/SelectBill.vue"; //
    import TempalteBill from "@/components/tempalteBill.vue"; //
    import {
        parseTime
    } from "@/utils/index";
    //
    import {
        getExceptionList, //
        postBillUpdate, //
        postExceptionDo, //
        postExceptionSolve, //
        exportBill,
        postAddToFinalBill,
    } from "@/api/archives.js";
    import {
        baseProfileUrl
    } from '@/api/baseUrl.js';
    //
    import tool from "@/utils/tool";
    import Table2Excel from 'table2excel.js'
    import {
        mapState
    } from 'vuex';
    export default {
        extends: TableCommon,
        name: 'ExceptionList',
        components: {
            CusTable,
            SearchInputs,
            BillFlow,
            SubTableDataInput, //
            ExceptionDo, //
            TempalteBill, //
            TempalteBill2: TempalteBill, //
            SelectBill, // 
        },
        data() {
            return {
                //
                dataInputModelVisible: false, //

                showTableDialog: false, // 
                showTableFlowDialog: false, // 
                exceptionDoDialog: false, // 
                selectBillDialog: false, //
                billId: '',
                detailData: {}, //

                solveType: '1', //
                tableDialogType: 1, //
                showPrintWd: false, //
                printObj: { //
                    id: "printArea",
                    popTitle: '',
                },
                haveFp: false, //
            }
        },
        computed: {
            ...mapState({
                userId: state => state.user.userId, //user
            })
        },
        mounted() {

        },
        methods: {
            parseTime,
            isYes: tool.isYes,
            _getData(params = {}) { //    
                params = {
                    ...this.search,
                    ...this.page,
                    billState: '',
                }
                getExceptionList(params).then(res => {
                    const data = res.rows ? res.rows : [];
                    //
                    data.forEach((item, index) => {
                        item.index = (this.page.pageNum - 1) * this.page.pageSize + index + 1;
                        if (item.type == 4) { // 
                            item.index += ' ';
                        }
                        if (item.subBills && item.subBills.length) {
                            item.subBills.forEach((p, k) => {
                                p.index = item.index + '.' + (k + 1);
                                p.isChild = true;
                            })
                        }
                    });
                    this.tableData = data;
                    this.total = res.total;
                })
            },
            showFp() { //
                let previewUrl = baseProfileUrl + this.detailData.stateGridPaper;
                if (!this.detailData.stateGridPaper) {
                    this.$message.warning('')
                    return;
                }
                this.$viewerApi({
                    images: [previewUrl],
                })
            },
            //excel
            html2Excel() {
                const name = this.detailData.kwell ? this.detailData.kwell : this.detailData.kpName;
                new Table2Excel('#printArea table').export(
                    `${name}-${this.detailData.month}-${this.tableDialogType==1?'':''}`);
            },
            handleClose() {
                this.showTableDialog = false;
            },
            closeFlowDialog() {
                this.showTableFlowDialog = false;
            },
            //
            downHand() {
                exportBill({
                    billState: '5',
                    ...this.search,
                }).then(tool.down)
            },

            btnClick(name, row) {
                this.detailData = row;
                this.haveFp = this.isYes(row.stateGirdPaperImp);
                if (name == "") {
                    this.showTableDialog = true;
                    this.tableDialogType = 1; //
                    this.showPrintWd = true;
                } else if (name == "") {
                    this.showTableDialog = true;
                    this.showPrintWd = true;
                    this.tableDialogType = 2; //
                } else if (name == "") {
                    if (row.solveType < 6) {
                        this.$confirm('?', '', {
                            confirmButtonText: '',
                            cancelButtonText: '',
                            type: 'warning'
                        }).then(() => {
                            //
                            postExceptionDo({
                                id: Number(row.id),
                                userId: Number(this.userId)
                            }).then(res => {
                                this.resPublick(res);
                            })
                        }).catch(() => {});
                    } else if (row.solveType == 6 || row.solveType == 7) {
                        this.selectBillDialog = true;
                        this.solveType = row.solveType;
                    }
                } else if (name == "") { //
                    this.solveType = row.solveType;
                    this.exceptionDoDialog = true;
                }
            },
            //
            showBillFlow() {
                this.billId = this.detailData.id;
                this.showTableFlowDialog = true;
            },
            //
            exceptionHand(data) {
                if (!data) {
                    this.exceptionDoDialog = false;
                    return;
                }
                this.loading = true;
                //
                postExceptionSolve({
                    userId: this.userId,
                    ...data,
                    solveType: this.solveType,
                }).then(res => {
                    this.exceptionDoDialog = false;
                    this.resPublick(res);
                })
            },
            //
            selectHand(data) {
                if (!data) {
                    this.selectBillDialog = false;
                    return;
                }
                this.$confirm('?', '', {
                    confirmButtonText: '',
                    cancelButtonText: '',
                    type: 'warning'
                }).then(() => {
                    this.loading = true;
                    postExceptionDo({
                        id: Number(this.detailData.id),
                        ...data,
                        userId: Number(this.userId)
                    }).then(res => {
                        this.selectBillDialog = false;
                        this.resPublick(res);
                    })
                }).catch(() => {});
            },
            closed() {
                this.showPrintWd = false;
            },
            //
            dataInputHand(data) {
                if (!data) {
                    this.dataInputModelVisible = false;
                    return;
                }
                this.loading = true;
                postBillUpdate(data).then(res => {
                    this.dataInputModelVisible = false; //
                    this.resPublick(res);
                })
            },
            //
            resPublick(res) {
                this.loading = false; //
                this.$message({
                    type: 'success',
                    message: ''
                })
                this.reloadTable();
            },
        }
    }
</script>