<template>
    <!---->
    <el-dialog title="-" custom-class="cusDialog" :visible.sync="show" width="9rem" :before-close="handleClose">
        <div class="content-title">
            <span v-if="this.currentRow&&this.currentRow.contractName" class="selected-label">
                {{this.currentRow.contractName}} \ {{this.currentRow.contractPrice}} \
                {{this.currentRow.wkTy}}</span>
        </div>
        <div class="item">
            <el-table v-table-scroll ref="searchTable" highlight-current-row row-key="index" max-height="300"
                :data="searchRecordTableData" :header-cell-style="{background:'#E6EAFE',color:'#606266'}"
                @current-change="currentRowHander" size="small">
                <el-table-column type="index" label="" width="70" />
                <el-table-column label="" prop="company" show-overflow-tooltip width="220"></el-table-column>
                <el-table-column label="" prop="contractName" show-overflow-tooltip width="280"></el-table-column>
                <el-table-column label="" prop="contractCode" show-overflow-tooltip width="250"></el-table-column>
                <el-table-column label="" prop="billDate" width="120">
                    <template slot-scope="scope">
                        <span>{{ changeMonth(scope.row['timeEnd']) }}</span>
                    </template>
                </el-table-column>
                <el-table-column label="" prop="wkTy" width="100" show-overflow-tooltip></el-table-column>
                <el-table-column label="kWh" prop="allEq" width="150"></el-table-column>
                <el-table-column label="" prop="ts" width="300">
                    <template slot-scope="scope">
                        <span>{{changeTime(scope.row['timeBegin'])}}-{{changeTime(scope.row['timeEnd'])}}</span>
                    </template>
                </el-table-column>
                <el-table-column label="/kWh" prop="contractPrice" width="180"></el-table-column>
                <el-table-column label="-" prop="totalPrice" width="180"></el-table-column>
            </el-table>
            <div v-if="!haveContract" class="warning"></div>
        </div>
        <span slot="footer" class="dialog-footer">
            <el-button type="primary" :disabled="!currentRow.contractName" :loading="loading" @click="submit"> 
            </el-button>
            <el-button @click="handleClose"> </el-button>
        </span>
    </el-dialog>
</template>
<script>
    import {
        parseTime,
        formatNum
    } from "@/utils/index";
    import tool from '@/utils/tool'
    import {
        getQueryFinalBill
    } from '@/api/archives';

    export default {
        props: {
            show: { //
                type: Boolean,
                default: false,
            },
            detailData: { //
                type: Object,
                default: () => {},
            },
            loading: Boolean
        },
        data() {
            return {
                currentRow: {},
                searchRecordTableData: [],
                haveContract: true, //
            }
        },
        mounted() {
            if (!this.detailData) return;
            this.getDataById(this.detailData.id);
        },
        methods: {
            //
            currentRowHander(val) {
                this.currentRow = val;
            },
            parseTime,
            changeMonth(t) {
                return parseTime(new Date(t), '{y}{m}')
            },
            changeTime(t) {
                return parseTime(new Date(t), '{y}/{m}/{d} {h}:{i}')
            },
            getDataById(billId) {
                if (!billId) return;
                this.currentRow = {}; //
                this.searchRecordTableData = []; //
                this.haveContract = true;
                getQueryFinalBill({
                    billId
                }).then((res) => {
                    this.haveContract = !!res.data;
                    this.searchRecordTableData = res.data ? res.data : [];
                });
            },
            handleClose() {
                this.$emit('selectHand')
            },
            //
            submit() {
                this.$emit('selectHand', {
                    finalBillId: this.currentRow.id
                })
            },
        },
        watch: {
            detailData: {
                handler(val) {
                    if (!val) return;
                    this.getDataById(val.id);
                },
                deep: true,
                immediate: true,
            },
        }
    }
</script>

<style scoped lang="scss">
    .selected-label {
        font-size: 12px;
        color: green;
    }

    .warning {
        margin-top: 16px;
    }

    .item /deep/ .el-table tr {
        cursor: pointer;
    }
</style>