<!---->
<template>
    <div>
        <SearchInputs @downHand="downHand" @searchHand="searchHand"></SearchInputs>
        <CusTable :tableData="tableData" @btnClick="btnClick" @handleSizeChange="handleSizeChange"
            @handleCurrentChange="handleCurrentChange" :pageNum="page.pageNum" :pageSize="page.pageSize" :total="total">
        </CusTable>
        <!-- -->
        <el-dialog custom-class="cusDialog" :visible.sync="showTableDialog" width="10rem" :before-close="handleClose"
            @closed="closed">
            <div class="cus-title" slot="title">
                <div class="title-btn" :class="{active: tab == 1}" @click="titleTab(1)"></div>
                <div class="title-btn" :class="{active: tab == 2}" @click="titleTab(2)">{{moreTab?'(1)':''}}
                </div>
                <div v-show="moreTab" class="title-btn" :class="{active: tab == 3}" @click="titleTab(3)">(2)
                </div>
            </div>
            <div class="print-body">
                <!--  -->
                <div key="1" v-if="showPrintWd&&tab==1">
                    <TempalteBill :type="2" :tableData="detailData"></TempalteBill>
                </div>
                <div key="2" v-if="showPrintWd&&tab==2">
                    <TempalteBill2 :type="3" :tableData="detailData"></TempalteBill2>
                </div>
                <div key="3" v-if="showPrintWd&&tab==3">
                    <TempalteBill3 :type="5" :tableData="detailData"></TempalteBill3>
                </div>
            </div>

            <span slot="footer" class="dialog-footer">
                <el-button type="primary" @click="html2Excel"> </el-button>
                <el-button type="primary" @click="printDo"> </el-button>
                <el-button @click="handleClose"> </el-button>
                <span id="print-btn" v-print="printObj"></span>
            </span>
        </el-dialog>
        <!---->
        <el-dialog title="" custom-class="cusDialog" :visible.sync="complelteModelVisible" width="7rem"
            :before-close="handleCloseUpload">
            <Upload ref="upload" :billId="billId"></Upload>
            <span slot="footer" class="dialog-footer">
                <el-button type="primary" :loading="loading" @click="submit"> </el-button>
                <el-button @click="handleCloseUpload"> </el-button>
            </span>
        </el-dialog>
        <!---->
        <el-dialog title="" custom-class="cusDialog" :visible.sync="editModelVisible" width="7rem"
            :before-close="handleCloseEdit">
            <EditBills ref="EditBills" :billId="billId" @reloadTable="reloadTable"></EditBills>
            <span slot="footer" class="dialog-footer">
                <el-button @click="handleCloseEdit"> </el-button>
            </span>
        </el-dialog>
    </div>
</template>
<script>
    //
    import TableCommon from "@/components/TableCommon.vue";
    import CusTable from "./components/completeBill/BillTable.vue";
    import SearchInputs from "../components/SearchInputsCollec.vue"
    import ErrorDlModel from "./components/common/ErrorDlModel.vue";
    import Upload from "./components/completeBill/Upload.vue";
    import EditBills from "./components/completeBill/EditBills.vue";
    import TempalteBill from "@/components/tempalteBill.vue";
    import TempalteBill2 from "@/components/tempalteBill.vue";
    import TempalteBill3 from "@/components/tempalteBill.vue";
    import {
        parseTime
    } from "@/utils/index";
    //
    import {
        getClosedBill, //
        postDoneBill, //
        exportFinalBill, //
        getBillInfo, //
    } from "@/api/archives.js";
    //
    import tool from "@/utils/tool";
    import Table2Excel from 'table2excel.js'
    import {
        mapState
    } from 'vuex';
    export default {
        extends: TableCommon,
        name: 'CompleteBill',
        components: {
            CusTable,
            SearchInputs,
            Upload, //
            TempalteBill, //
            TempalteBill2, //
            TempalteBill3, //
            EditBills, //
        },
        data() {
            return {
                //
                complelteModelVisible: false, //
                editModelVisible: false, //
                showTableDialog: false, //
                detailData: {}, //
                billId: '', //id
                showPrintWd: false, //
                tab: 1,
                moreTab: false, //tab 2
                printObj: { //
                    id: "printArea",
                    popTitle: '',
                }
            }
        },
        computed: {
            ...mapState({
                userId: state => state.user.userId, //user
            })
        },
        mounted() {},
        methods: {
            parseTime,
            _getData(params = {}) { //    
                params = {
                    ...this.search,
                    ...this.page,
                    status: 'A', //
                }
                getClosedBill(params).then(res => {
                    const data = res.rows ? res.rows : [];
                    this.tableData = data;
                    this.total = res.total;
                })
            },
            titleTab(tab) {
                this.tab = tab;
            },
            //excel
            html2Excel() {
                const name = this.detailData.company;
                const nameArr = ['', '', '', '2'];
                const date = this.detailData.billDate.replace('', '-').replace('', '');
                new Table2Excel('#printArea table').export(`${name}-${date}-${nameArr[this.tab]}`);
            },
            //
            downHand() {
                exportFinalBill({
                    ...this.search,
                    status: 'A', //
                }).then(tool.down)
            },
            handleClose() {
                this.showTableDialog = false;
            },
            handleCloseUpload() {
                this.complelteModelVisible = false;
            },
            handleCloseEdit() {
                this.editModelVisible = false;
            },
            // 
            printDo() {
                getBillInfo({
                    id: this.billId
                }).then(res => {
                    const msg = res.msg;
                    this.$confirm(msg.replace(/\n/g, ' ').replace("", '<br/>  '), '', {
                        confirmButtonText: '',
                        cancelButtonText: '',
                        type: ~msg.indexOf('') ? 'success' : 'warning',
                        dangerouslyUseHTMLString: true
                    }).then(() => {
                        document.getElementById('print-btn').click();
                    })
                })
            },
            btnClick(name, row) {
                this.detailData = row;
                this.billId = row.id;
                if (name == "") {
                    this.showTableDialog = true;
                    this.showPrintWd = true;
                    // 2
                    this.moreTab = row.company.indexOf('') !== -1;
                } else if (name == "") {
                    this.complelteModelVisible = true;
                } else if (name == "") {
                    this.editModelVisible = true;
                }
            },
            submit() {
                this.$refs['upload'].submitForm((data) => {
                    this.loading = true; //
                    postDoneBill({
                        userId: this.userId,
                        ...data
                    }).then(res => {
                        this.complelteModelVisible = false; //
                        this.resPublick(res);
                    })
                })
            },
            //
            closed() {
                this.showPrintWd = false;
            },
            //
            resPublick(res) {
                this.loading = false; //
                this.$message({
                    type: 'success',
                    message: ''
                })
                this.reloadTable();
            }
        }
    }
</script>

<style scoped lang="scss">
    .cus-title {
        display: flex;
        user-select: none;

        .title-btn {
            &.active {
                color: #2E4AF7;
            }

            cursor: pointer;
            margin-right: 60px;
            font-weight: 500;
            color: #333333;
            line-height: 20px;
            font-family: "PFC"
        }
    }
</style>