<!---->
<template>
    <div style="width: 100%" class="cus-table">
        <el-table v-table-scroll style="width: 100%" :tree-props="{children: 'subBills'}" row-key="index"
            :data="tableData" :header-cell-style="{background:'#E6EAFE',color:'#606266'}">
            <el-table-column label="" prop="index" width="90" fixed="left" />
            <el-table-column label="" prop="kpName" width="200" fixed="left"></el-table-column>
            <el-table-column label="" prop="workType" width="90"></el-table-column>
            <el-table-column label="" prop="arSnum" width="120">
                <template slot-scope="scope">
                    <span>{{ changeMonth(scope.row['month']) }}</span>
                </template>
            </el-table-column>
            <el-table-column label="" prop="arSnum" width="100">
                <template slot-scope="scope">
                    <span>{{ scope.row['type']=='3' && !scope.row['isChild'] ? '': scope.row['arSnum']}}</span>
                    <!--type3   -->
                </template>
            </el-table-column>
            <el-table-column label="" prop="arEnum" width="100">
                <template slot-scope="scope">
                    <span>{{ scope.row['type']=='3' && !scope.row['isChild'] ? '': scope.row['arEnum']}}</span>
                    <!--type3   -->
                </template>
            </el-table-column>
            <el-table-column label="" prop="blRate" width="80">
                <template slot-scope="scope">
                    <span>{{ scope.row['type']=='3' && !scope.row['isChild']? '': scope.row['blRate']}}</span>
                    <!--type3   -->
                </template>
            </el-table-column>
            <el-table-column label="kwh" prop="useEq" width="120">
                <template slot-scope="scope">
                    <span>{{ scope.row['type']=='3' && !scope.row['isChild']? '': scope.row['useEq']}}</span>
                    <!--type3   -->
                </template>
            </el-table-column>
            <el-table-column label="" prop="loseRate" width="120"></el-table-column>
            <el-table-column label="" prop="loseEq" width="100"></el-table-column>
            <el-table-column label="" prop="extraEq" width="100"></el-table-column>
            <el-table-column label="kwh" prop="allEq" width="130"></el-table-column>
            <el-table-column label="" prop="kcName" show-overflow-tooltip width="200"></el-table-column>
            <el-table-column label="" prop="glName" width="200" show-overflow-tooltip></el-table-column>
            <el-table-column label="" prop="jpName" show-overflow-tooltip width="250"></el-table-column>
            <el-table-column label="" prop="sdName" show-overflow-tooltip width="240"></el-table-column>
            <el-table-column label="" prop="ts" width="300">
                <template slot-scope="scope">
                    <span v-if="scope.row['arSt']">
                        {{changeTime(scope.row['arSt'])}}-{{changeTime(scope.row['arEt'])}}
                    </span>
                </template>
            </el-table-column>
            <el-table-column label="" align="left" fixed="right" class-name="cusTable" width="80">
                <template slot-scope="scope">
                    <span :class="scope.row['flowStatus']==201?'red':'green'" type="text" size="small">
                        {{ scope.row['flowStatus']==201?'':'' }}
                    </span>
                </template>
            </el-table-column>
            <el-table-column label="" align="left" fixed="right" class-name="cusTable" width="90">
                <template slot-scope="scope">
                    <div class="table-btns">
                        <div class="center">
                            <span v-if="defined(scope.row.dayPaper)" class="cus-btn"
                                @click="handClick('',scope.row)" type="text" size="small">{{''}}</span>
                        </div>
                    </div>
                </template>
            </el-table-column>
            <el-table-column label="" align="left" fixed="right" class-name="cusTable" width="120">
                <template slot-scope="scope">
                    <div class="table-btns">
                        <div class="center">
                            <span v-if="defined(scope.row.sitePaper)" class="cus-btn"
                                @click="handClick('',scope.row)" type="text" size="small">
                            </span>
                        </div>
                    </div>
                </template>
            </el-table-column>
            <el-table-column label="" align="center" fixed="right" class-name="cusTable" width="105">
                <template slot-scope="scope">
                    <div class="table-btns" v-if="scope.row.flowStatus==201">
                        <!---->
                        <template v-if="scope.row.isChild">
                            <!---->
                            <span v-if="scope.row.type!=3" class="cus-btn line" @click="handClick('',scope.row)"
                                type="text" size="small"></span>
                            <span v-if="scope.row.type!=3" class="cus-btn" @click="handClick('',scope.row)"
                                type="text" size="small"></span>
                        </template>
                        <!---->
                        <template v-else-if="scope.row.subBills&&scope.row.subBills.length">
                            <!---->
                            <span v-if="scope.row.type==3" class="cus-btn line" @click="handClick('',scope.row)"
                                type="text" size="small"></span>
                            <span class="cus-btn" @click="handClick('',scope.row)" type="text" size="small"></span>
                        </template>
                        <!---->
                        <template v-else>
                            <span class="cus-btn line" @click="handClick('',scope.row)" type="text"
                                size="small"></span>
                            <span class="cus-btn" @click="handClick('',scope.row)" type="text" size="small"></span>
                        </template>
                    </div>
                </template>
            </el-table-column>
        </el-table>
        <div class="page pagination-box">
            <el-pagination @size-change="handleSizeChange" @current-change="handleCurrentChange" :current-page="pageNum"
                :page-sizes="[10, 20, 50, 100]" :page-size="pageSize" layout="prev, pager, next, jumper,total, sizes"
                :total="total">
            </el-pagination>
        </div>
    </div>
</template>
<script>
    import tool from "@/utils/tool";
    import Table from "@/components/ElTableCommon.vue";
    export default {
        extends: Table,
        name: 'userBillTable',
        data() {
            return {
                statusArr: ['', ''],
            }
        },
        methods: {
            defined: tool.defined,
            isYes: tool.isYes,
            handClick(btnName, row) {
                if (btnName == '') {
                    this.$prompt('', '', {
                        confirmButtonText: '',
                        cancelButtonText: '',
                    }).then(({
                        value
                    }) => {
                        this.$emit('btnClick', btnName, {
                            ...row,
                            reason: value.trim()
                        });
                    }).catch(() => {});
                } else {
                    this.$emit('btnClick', btnName, row);
                }
            }
        }
    }
</script>
<style scoped lang="scss">
    .page {
        display: flex;
        justify-content: flex-end;
    }

    .cus-btn {
        color: #2E4AF7;
        font-size: 14px;
        padding: 0 2px;
        cursor: pointer;
        line-height: 22px;
        position: relative;
    }

    .table-btns {
        display: flex;
        justify-content: space-between;

        .center {
            width: 100%;
            display: flex;
            justify-content: center;
        }

        .line:after {
            content: "";
            position: absolute;
            display: block;
            right: -12px;
            top: 6px;
            bottom: 0;
            height: 12px;
            width: 1px;
            background: #E3E5EC;
        }
    }


    /deep/ .active-row {
        background-color: #F5F7FA;
    }
</style>